import { Post } from '@/models/Post.class';
export class Article extends Post {
  constructor(raw) {
    super(raw);
    this.title = '';
    this.date = {
      month: '',
      day: 0,
      year: 0
    };
    this.updated = '';
    this.comments = false;
    this.path = '';
    this.covers = null;
    this.excerpt = null;
    this.content = '';
    this.count_time = {};
    if (raw) {
      for (const key of ['covers', 'content']) {
        if (Object.prototype.hasOwnProperty.call(raw, key)) {
          Object.assign(this, {
            [key]: raw[key]
          });
        }
      }
    }
  }
}
export class Page {
  constructor(raw) {
    this.title = '';
    this.uid = '';
    this.date = {
      month: '',
      day: 0,
      year: 0
    };
    this.updated = '';
    this.comments = false;
    this.path = '';
    this.covers = null;
    this.excerpt = null;
    this.content = '';
    this.count_time = {};
    this.toc = '';
    if (raw) {
      for (const key of Object.keys(this)) {
        if (Object.prototype.hasOwnProperty.call(raw, key)) {
          if (key === 'date') {
            const m = new Date(raw[key]);
            const translateMonth = `settings.months[${m.getMonth()}]`;
            raw[key] = Object.create({
              month: translateMonth,
              day: m.getUTCDate(),
              year: m.getUTCFullYear()
            });
          }
          Object.assign(this, {
            [key]: raw[key]
          });
        }
      }
    }
  }
}